<template>
  <div>
    <searchnav title="登录注册"
               :show="false"></searchnav>
    <van-tabs v-model="active"
              type="card">
      <van-tab title="手机登录">
        <van-field v-model="phone"
                   center
                   clearable
                   label="手机号码"
                   placeholder="请输入手机号码">
          <template #button>
            <van-button size="small"
                        type="primary"
                        @click='test'>检测是否注册</van-button>
          </template>
        </van-field>

        <van-field v-model="sms"
                   center
                   clearable
                   label="短信验证码"
                   placeholder="请输入短信验证码">
          <template #button>
            <van-button size="small"
                        type="primary"
                        @click='getCode'>发送验证码</van-button>
          </template>
        </van-field>
        <van-button color="linear-gradient(to right, #4bb0ff, #6149f6)"
                    size='large'
                    @click="send"
                    block>提交登录</van-button>
      </van-tab>
      <van-tab title="账号登录">
        <van-form @submit="onSubmit">
          <van-field v-model="login.username"
                     name="用户名"
                     label="用户名"
                     placeholder="用户名"
                     :rules="[{ required: true, message: '请填写用户名' }]" />
          <van-field v-model="login.password"
                     type="password"
                     name="密码"
                     label="密码"
                     placeholder="密码"
                     :rules="[{ required: true, message: '请填写密码' }]" />
          <div style="margin: 16px;">
            <van-button round
                        block
                        type="info"
                        native-type="submit">
              提交
            </van-button>
          </div>
        </van-form>
      </van-tab>
      <van-tab title="用户注册">
        <van-form @submit="onregiste"
                  label-align='right'
                  :colon='true'>
          <van-field v-model="regiest.username"
                     name="usernam"
                     label="用户名"
                     placeholder="用户名"
                     :rules="[{ required: true, message: '请填写用户名' }]" />
          <van-field v-model="regiest.nickname"
                     name="nickname"
                     label="昵称"
                     placeholder="昵称"
                     :rules="[{ required: true, message: '请填写昵称' }]" />
          <van-field v-model="regiest.password"
                     type="password"
                     name="password"
                     label="密码"
                     placeholder="密码"
                     :rules="[{ required: true, message: '请填写密码' }]" />
          <van-field v-model="regiest.repass"
                     type="password"
                     name="repass"
                     label="确认密码"
                     placeholder="确认密码"
                     :rules="[{ required: true, message: '请填写确认密码' }]" />
          <van-field name="radio"
                     label="性别">
            <template #input>
              <van-radio-group v-model="regiest.radio"
                               direction="horizontal">
                <van-radio name="man">男</van-radio>
                <van-radio name="woman">女</van-radio>
              </van-radio-group>
            </template>
          </van-field>
          <van-field name="checkboxGroup"
                     label="自我标签">
            <template #input>
              <van-checkbox-group v-model="regiest.checkboxGroup"
                                  direction="horizontal">
                <van-checkbox name="study"
                              shape="square">学习</van-checkbox>
                <van-checkbox name="sing"
                              shape="square">唱歌</van-checkbox>
                <van-checkbox name="dance"
                              shape="square">跳舞</van-checkbox>
                <van-checkbox name="draw"
                              shape="square">绘画</van-checkbox>
              </van-checkbox-group>
            </template>
          </van-field>
          <van-uploader :after-read="afterRead"
                        v-model="regiest.fileList"
                        multiple />
          <div style="margin: 16px;">
            <van-button round
                        block
                        type="info"
                        native-type="submit">
              提交
            </van-button>
          </div>
        </van-form>
      </van-tab>

    </van-tabs>

  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  data () {
    return {
      active: 0,
      regiest: {},
      login: {
        username: localStorage.username
      },
      // username: '',
      // nickname: '',
      // password: '',
      // repass: '',
      // radio: '1',
      // checkboxGroup: [],
      fileList: [
        require('@/assets/imgs/slide1.png'),
        require('@/assets/imgs/slide2.png'),
        require('@/assets/imgs/slide3.png'),
      ],
      sms: "",
      phone: ""
    };
  },
  methods: {

    afterRead (file) {
      // console.log(file);
    },
    getCode () {
      // console.log(this.phone)
      this.axios({
        url: '/yun/captcha/sent',
        params: {
          phone: this.phone
        }
      }).then(res => {
        Toast('发送成功')
        // console.log(res)
      })
    },
    test () {
      this.axios({
        url: "/yun/cellphone/existence/check",
        params: {
          phone: this.phone
        }
      }).then(res => {

        // console.log(res)
      })
    },
    send () {

      this.axios({
        url: "/yun/captcha/verify",
        params: {
          phone: this.phone,
          captcha: this.sms
        }
      }).then(res => {
        console.log(res)
        if (res.data.code === 200) {
          this.$router.push({ name: 'now' })
        }
      }).catch(err => {
        Toast('验证码有误')
      })
    },
    onregiste () {
      console.log(this.regiest)
      this.axios({
        url: '/vue/registe',
        method: 'post',
        data: this.regiest
      }).then((res) => {
        console.log(res)
        if (res.data.type === 1) {
          this.active = 1;
          this.login.username = this.regiest.username
        }
      })
    },
    onSubmit () {
      this.axios.post('/vue/login', this.login).then(res => {
        console.log(res)
        if (res.data.type) {
          localStorage.isLogin = true;
          localStorage.username = res.data.data.username

          sessionStorage.token = res.data.token;
          this.$router.push({ name: 'mine' })
        }
      })
    },
  }
};
/*
  1：注册流程细化：
      1：前端将表单数据通过axios传输给后端，后端拿到数据，判定用户情况，注册成功给返回数据
      2：前端根据返回数据情况，此时可设置简单localstorage以及sessionStrorage，比对并跳转登录界面，
      3：登录页面，提交用户名以及密码给后端，后端判定成功，设置req.session记录用户名，以及token，用于后期比对，并将token放在data里发给前端。
      4：前端此时存储token到sessionStorage，以及用户名到Localstorage，并跳转个人中心，此时后端，根据前端请求路径设置token拦截，express中，设置中间件（模块暴露函数）放在vue之前，session之后。同时比对客户端的token与服务器端token差异决定操作，其中客户端token需要前端设置axios默认设置，在发送请求之前携带好sessionStorage里的token。
*/
</script>
<style lang="scss" >
#registe {
  padding: 10px 0px;
  height: 40px;
  font-size: 30px;
  line-height: 40px;
  text-align: center;
  color: black;
}
form {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.van-checkbox {
  margin-bottom: 20px;
}
.van-uploader {
  background: transparent;
}
.van-tabs--card {
  .van-tabs__wrap {
    padding: 20px 0px;
  }
  .van-tabs__nav--card {
    height: 50px;
  }
  .van-tab {
    line-height: 50px;
    font-size: 16px;
  }
}
.van-tabs__content {
  margin-top: 10px;
}
.van-tabs {
  padding: 0px 15px;
}
.van-cell {
  margin: 10px 0px;
  border-radius: 8px;
}
.van-button--large {
  width: 50%;
  margin: 0 auto;
  border-radius: 30px;
}
</style>